@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,400;1,700&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


body {
    background: linear-gradient(to bottom, #656565, #999797);
    min-height: 100vh;
}

.container {
    max-width: 980px;
    margin: auto;
    position: relative;
}

.main {
    min-height: 90vh;
    background: white;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0.6 1;
    background: #f6f6f6;
    color: #999;
    height: 3rem;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom: 1px solid rgb(214, 209, 209);
}

.logo {
    text-transform: uppercase;
    margin-left: 15px;
    font-size: 20px;
}

.logo_img {
    text-decoration: none;
    color: #888;
}

.logo_img>span {
    color: #fe1110;
}

.search {
    border: 1px solid rgb(214, 209, 209);
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 3px;
    width: 200px;
}

.search>input {
    outline: 0;
    border: 0;
    background: #f6f6f6;
    color: #888;
    font-size: 20px;

    width: 180px;
}


.navigation>* {
    text-decoration: none;
    margin-left: 15px;
    color: #555;
    padding: 3px 7px;
    border-radius: 3px;
    transition: .3s;
}

.link:first-child {
    margin: 0;
}

.navigation>*:hover {
    /* border: 1px solid rgb(214, 213, 213); */
    box-shadow: 2px 2px 5px 2px #AAAAAA;
}

.link {
    margin-right: 15px;
}
.footer-grid {
    height: 70px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 10px;
    font-size: 10px;
}

.terms {
    margin: 0;

}

.footer-links {
    margin: 0;
    text-align: end;
}

.footer-link {
    text-decoration: none;
    color: #fff;
    margin-right: 5px;
    transition: .3s;
}

.footer-link:hover {
    color: black;
}

.container-footer {
    display: block;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer-btn {
    padding: 15px 25px;
}

.footer-backBtn {
    /* box-shadow: 2px 2px 10px -1px #aaaaaa; */
    text-decoration: none;
    color: #555;
    width: 172px;
    /* background: blue; */
    border: 1px solid #333;
    outline: none;
    border: 0;
    padding: 10px 20px;
    border-radius: 13px;
    transition: .3s;
    text-transform: uppercase;
    cursor: pointer;
}

.footer-backBtn:hover {
    color: black;
    background-color: white;
}
.commercial {
    padding-top: 15px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.aside {
    /* display: none; */
    position: absolute;
    right: -310px;
    display: inline-block;
    /* float: right; */
}

.aside_commercial {
    border-width: 0px;
    display: block;
    height: 600px;
}
.main_phone {
    display: flex;
    justify-content: space-evenly;
    padding-top: 5px;
    width: 100%;
}

.main_phoneContainer {
    position: relative;
    transition: .2s;
    color: #000;
}

.main_phoneContainer:hover {
    transform: scale(1.02);
}

.main_phonePicture {
    border-radius: 5px;
    width: 150px;
    height: 95px;
}

.main_phoneDesc {
    position: absolute;
    top: 75px;
    left: 5px;
}

.main_phoneTitle {
    margin: 0;
    text-transform: uppercase;
    color: #f6f6f6;
}

.main_phoneModel {
    margin: 0;
    font-size: 12px;
    font-weight: bold;
}

.price {
    display: flex;
    justify-content: space-evenly;
    padding-top: 15px;

}

.filter_price {
    word-wrap: break-word;
    width: 70px;
    height: 50px;
    text-decoration: none;
    color: #000;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    text-align: center;
    line-height: 1.1;
    border-radius: 5px;
}

.filter_title {
    padding: 0;
    margin: 0;
}

.flex {
    display: flex;
    align-items: flex-start;
    padding-top: 25px;
}
.article_container {
    display: flex;
    flex-wrap: wrap;
    flex: 0.6 1;
    font-size: 13px;
    width: 60%;
}

.article_test {
    max-width: 45%;
    text-align: justify;
    padding-left: 1rem;
    padding-bottom: 1rem;
}

.article_title {
    display: block;
    margin-top: 0;
    font-size: 16px;
    margin-bottom: 15px;
    text-decoration: none;
    color: rgb(43, 105, 221);
    font-weight: bold;
}

.article_image {
    object-fit: contain;
    float: left;
    margin-right: 5px;
}

.article_text {
    padding-top: 0;
}
.news {
    flex: 0.25 1;
    margin-right: 15px;
}

.news_title {
    margin-top: 0;
    text-align: center;
    font-size: 14px;
}

.news_text {
    font-size: 12px;
    text-align: justify;
}
.new_phones {
    text-align: center;
    flex: 0.15 1;
}

.phones_title {
    margin-top: 0;
    margin-bottom: 35px;
    font-size: 16px;
}

.phone {
    color: rgb(43, 105, 221);
    display: block;
    text-decoration: none;
    font-size: 14px;
    margin-bottom: 30px;
}

.phone_title {
    margin: 0;
}

.phone_model {
    margin-top: 0;
}
.button_container {
    display: flex;
    flex-wrap: wrap;
}

.button {
    box-shadow: 2px 2px 10px -1px #AAAAAA;
    text-decoration: none;
    color: #555;
    border-radius: 13px;
    display: block;
    width: 172px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    margin-left: 10px;
    margin-top: 10px;
    padding: 3px 5px;
    text-transform: uppercase;
    transition: .3s;
}

.button:hover {
    transform: scale(1.05);
}

.button_icon {
    font-size: 20px;
    margin-right: 10px;
    margin-left: 10px;
}

.button_title {
    margin: 0;
    font-size: 16px;
    text-align: center;
    line-height: 1;
}
.asideDown {
    /* display: none; */
    position: absolute;
    top: 900px;
    right: -310px;
    display: inline-block;
    /* float: right; */
}

.aside_commercial {
    border-width: 0px;
    display: block;
    height: 600px;
}
