.main_phone {
    display: flex;
    justify-content: space-evenly;
    padding-top: 5px;
    width: 100%;
}

.main_phoneContainer {
    position: relative;
    transition: .2s;
    color: #000;
}

.main_phoneContainer:hover {
    transform: scale(1.02);
}

.main_phonePicture {
    border-radius: 5px;
    width: 150px;
    height: 95px;
}

.main_phoneDesc {
    position: absolute;
    top: 75px;
    left: 5px;
}

.main_phoneTitle {
    margin: 0;
    text-transform: uppercase;
    color: #f6f6f6;
}

.main_phoneModel {
    margin: 0;
    font-size: 12px;
    font-weight: bold;
}

.price {
    display: flex;
    justify-content: space-evenly;
    padding-top: 15px;

}

.filter_price {
    word-wrap: break-word;
    width: 70px;
    height: 50px;
    text-decoration: none;
    color: #000;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    text-align: center;
    line-height: 1.1;
    border-radius: 5px;
}

.filter_title {
    padding: 0;
    margin: 0;
}

.flex {
    display: flex;
    align-items: flex-start;
    padding-top: 25px;
}