.footer-grid {
    height: 70px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 10px;
    font-size: 10px;
}

.terms {
    margin: 0;

}

.footer-links {
    margin: 0;
    text-align: end;
}

.footer-link {
    text-decoration: none;
    color: #fff;
    margin-right: 5px;
    transition: .3s;
}

.footer-link:hover {
    color: black;
}

.container-footer {
    display: block;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}