.button_container {
    display: flex;
    flex-wrap: wrap;
}

.button {
    box-shadow: 2px 2px 10px -1px #AAAAAA;
    text-decoration: none;
    color: #555;
    border-radius: 13px;
    display: block;
    width: 172px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    margin-left: 10px;
    margin-top: 10px;
    padding: 3px 5px;
    text-transform: uppercase;
    transition: .3s;
}

.button:hover {
    transform: scale(1.05);
}

.button_icon {
    font-size: 20px;
    margin-right: 10px;
    margin-left: 10px;
}

.button_title {
    margin: 0;
    font-size: 16px;
    text-align: center;
    line-height: 1;
}