.footer-btn {
    padding: 15px 25px;
}

.footer-backBtn {
    /* box-shadow: 2px 2px 10px -1px #aaaaaa; */
    text-decoration: none;
    color: #555;
    width: 172px;
    /* background: blue; */
    border: 1px solid #333;
    outline: none;
    border: 0;
    padding: 10px 20px;
    border-radius: 13px;
    transition: .3s;
    text-transform: uppercase;
    cursor: pointer;
}

.footer-backBtn:hover {
    color: black;
    background-color: white;
}