.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0.6;
    background: #f6f6f6;
    color: #999;
    height: 3rem;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom: 1px solid rgb(214, 209, 209);
}

.logo {
    text-transform: uppercase;
    margin-left: 15px;
    font-size: 20px;
}

.logo_img {
    text-decoration: none;
    color: #888;
}

.logo_img>span {
    color: #fe1110;
}

.search {
    border: 1px solid rgb(214, 209, 209);
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 3px;
    width: 200px;
}

.search>input {
    outline: 0;
    border: 0;
    background: #f6f6f6;
    color: #888;
    font-size: 20px;

    width: 180px;
}


.navigation>* {
    text-decoration: none;
    margin-left: 15px;
    color: #555;
    padding: 3px 7px;
    border-radius: 3px;
    transition: .3s;
}

.link:first-child {
    margin: 0;
}

.navigation>*:hover {
    /* border: 1px solid rgb(214, 213, 213); */
    box-shadow: 2px 2px 5px 2px #AAAAAA;
}

.link {
    margin-right: 15px;
}