.article_container {
    display: flex;
    flex-wrap: wrap;
    flex: 0.6;
    font-size: 13px;
    width: 60%;
}

.article_test {
    max-width: 45%;
    text-align: justify;
    padding-left: 1rem;
    padding-bottom: 1rem;
}

.article_title {
    display: block;
    margin-top: 0;
    font-size: 16px;
    margin-bottom: 15px;
    text-decoration: none;
    color: rgb(43, 105, 221);
    font-weight: bold;
}

.article_image {
    object-fit: contain;
    float: left;
    margin-right: 5px;
}

.article_text {
    padding-top: 0;
}